import {FC} from 'react'
import bestMoviesImg from '../../../images/250bestmovies.png'
import popularFilmsImg from '../../../images/popularfilms.png'
import movies from '../../../images/movies.png'
import FilmsComponents from '../../filmsComponents/FilmsComponents'

const Films: FC  = () => {
  return (
    <div className='items-center ml-5'>
        <FilmsComponents img={movies} title=" " subtitle=' ' link='/films/allfilms' />
        <FilmsComponents img={bestMoviesImg} title=" " subtitle=' ' link="/films/bestmovies"/>
        <FilmsComponents img={popularFilmsImg} title=" " subtitle=' ' link="/films/popular"/>
    </div>
  )
}

export default  Films